* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.search-form {
  width: 100%;
  background-color: #9370db;
  height: 75px;
  display: flex;
  justify-content: center;
  align-items: center;
 
}

.search-form__input {
  height: 35px;
  width: 300px;
  border: none;
  padding: 0 15px 0;
  border-radius: 4px 0px 0px 4px;
  outline: none;
  font-size: 18px;
}

.search-form__button {
  height: 35px;
  width: 45px;
  border: none;
  border-radius: 0 4px 4px 0;
}

.gallery {
  max-width: 1500px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 10px;
  

  .photo-card {
    max-width: 270px;
    height: auto;
    width: 100%;
    margin: 10px;
    border: 1px solid #d3d3d3;

    b {
      color: #727272;
    }

    span {
      color: darkgrey;
    }

    img {
      width: 100%;
      height: 270px;
      object-fit: cover;
    }

    .info {
      font-size: 14px;
      padding: 10px 25px;

      .info-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-weight: 700;
      }
    }
  }
}
.load-more {
  height: 40px;
  width: 200px;
  border: none;
  cursor: pointer;
  display: block;
  margin: auto auto 50px;
  font-size: 18px;
  background-color: #9370db;
  color: #f5f5f5;
}

.hidden{
  visibility: hidden;
}